@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
*{
  box-sizing: border-box;
}
body{
  background-color: #111;
  color: #fff;
  margin: 0;
  font-family: 'Roboto', sans-serif;

}
.lists{
  margin-top: -100px;
}
footer{
  display: grid;
  justify-content: space-evenly;
  padding: 20px;
}
footer p{
  text-align: center;
}
footer a{
  color: var(--base-color);
  text-decoration: none;
  transition: .5s;
}
footer a:hover{
  color: var(--base-color-second);
  text-decoration: underline;
}
.loading{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 99;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.movieRow {
    margin-bottom: 30px;
    z-index: -1px;
}
.movieRow h2 {
    margin: 0 0 0 30px;
}
.movieRow--listarea {
    overflow-x: hidden;
    padding-left: 30px;
}
.movieRow--list{
    transition: all ease .4s;
}
.movieRow--item {
    display: inline-block;
    width: 150px;
}
.movieRow--item img {
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all ease .2s;
}
.movieRow--item img:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
    cursor: pointer;
}
.movieRow--Left, 
.movieRow--right{
    position: absolute;
    width: 40px;
    background-color: rgba(0, 0, 0, .6);
    height: 225px;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease .3s;

}
.movieRow--Left{
    left: 0;
}
.movieRow--right{
    right: 0;
}
.movieRow:hover .movieRow--right,
.movieRow:hover .movieRow--Left{
    opacity: 1;
}
@media (max-width: 760px){
    .movieRow--Left,
    .movieRow--right{
        opacity: 1;
    }
}
.featured{
    height: 100vh;
}
.featured--vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
}
.featured--name{
    font-size:  60px;
    font-weight: bold;
}
.featured--info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}
.featured--points, .featured--year, .featured--seasons{
    margin-right: 15px;

}
.featured--points{
    display: inline-block;
    color: #46d369;
}
.featured--year{
    display: inline-block;
}
.featured--seasons{
    display: inline-block;

}
.featured--description{
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}
.featured--buttons{
    margin-top: 15px;
}
.featured--watchbutton, .featured--listbutton{
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease .2s;
}
.featured--watchbutton:hover , 
.featured--listbutton:hover{
    opacity: .7;
}
.featured--watchbutton{
    background-color: #fff;
    color: #000;
}
.featured--listbutton{
    background-color: #333;
    color: #fff;
}
.featured--genres{
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}
@media (max-width: 760px){
    .featured{
        height: 90vh;
    }
    .featured--name{
        font-size: 30px;
    }
    .featured--info{
        font-size: 16px;
        max-width: 100%;
        margin-right: 30px;
    }
    .featured--description{
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    .featured--watchbutton,
    .featured--listbutton{
        font-size: 16px;
    }
    .featured--genres{
        font-size: 14px;
    }
}
header{
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease .3s;
}
header.black{
    background-color: black;
}
.header--logo{
    height: 70px;
}
.header--logo img{
    height: 100%;
}
.header--user{
    height: 50px;
}
.header--user img{
    height: 100%;
}
