.movieRow {
    margin-bottom: 30px;
    z-index: -1px;
}
.movieRow h2 {
    margin: 0 0 0 30px;
}
.movieRow--listarea {
    overflow-x: hidden;
    padding-left: 30px;
}
.movieRow--list{
    transition: all ease .4s;
}
.movieRow--item {
    display: inline-block;
    width: 150px;
}
.movieRow--item img {
    width: 100%;
    transform: scale(0.9);
    transition: all ease .2s;
}
.movieRow--item img:hover {
    transform: scale(1);
    cursor: pointer;
}
.movieRow--Left, 
.movieRow--right{
    position: absolute;
    width: 40px;
    background-color: rgba(0, 0, 0, .6);
    height: 225px;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease .3s;

}
.movieRow--Left{
    left: 0;
}
.movieRow--right{
    right: 0;
}
.movieRow:hover .movieRow--right,
.movieRow:hover .movieRow--Left{
    opacity: 1;
}
@media (max-width: 760px){
    .movieRow--Left,
    .movieRow--right{
        opacity: 1;
    }
}