header{
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease .3s;
}
header.black{
    background-color: black;
}
.header--logo{
    height: 70px;
}
.header--logo img{
    height: 100%;
}
.header--user{
    height: 50px;
}
.header--user img{
    height: 100%;
}